/* Set Roboto as the default sans-serif font for body text */
body {
    font-family: 'Roboto', sans-serif;
    font-weight: 400; /* Regular weight for body */
    color: #333; /* Optional: adjust color for readability */
    line-height: 1.6; /* Optional: improve readability */
  }
  
/* Set Lora as the serif font for titles and headings */
h1, h2, h3, h4, h5, h6 {
    font-family: 'Lora', serif;
    font-weight: 700; /* Bold weight for headings */
    color: #222; /* Optional: make headings stand out */
  }

.WindmatePage {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
}

.WindmateContent {
    text-align: center;
    margin-top: 30px;
}


.logo-title {
    display: flex;
    align-items: flex-end;
    margin-bottom: 25px;
  }

.windmateLogo {
    height: 200px;
    width: auto;
    margin-right: -130px;
}
  

.WindmateInfoSection {
    margin-top: 20px;
    text-align: left;
    padding-bottom: 25px;
}

.WindmateInfoSection h1 {
    color: #333;
    text-align: left;
    margin-left: 50px;
    padding-bottom: 10px;
}

.WindmateInfoContent {
    background-color: #40345c31;
    padding: 20px;
    border-radius: 12px;
    transition: transform 0.2s ease;
}


.WindmateInfoContent a {
    color: #333; /* Change this to your desired color */
    text-decoration: none; /* Removes underline */
    font-weight: bold;
}

.PhonePreviewsContainer {
    display: flex;
    justify-content: center; /* Centers the grid horizontally */
    width: 100%; /* Ensures the parent container takes full width */
  }
  
  .PhonePreviews {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 16px; 
  }
  
  .PhonePreviews img {
    width: 100%;
    object-fit: cover;
  }

  @media (max-width: 768px) {

    .PhonePreviews {
      display: grid;
      grid-template-columns: 1fr; /* Single column */
      grid-template-rows: repeat(3, auto); /* Three rows */
      gap: 10px; 
      width: 70%; /* Adjust width if needed */
      max-width: 70%; /* Prevents overflow on smaller screens */
      margin: 0 auto; /* Centers horizontally */
      text-align: center; /* Centers content inside */
    }
}
  

  
  
  
  