/* Set Roboto as the default sans-serif font for body text */
body {
  font-family: 'Roboto', sans-serif;
  font-weight: 400; /* Regular weight for body */
  color: #333; /* Optional: adjust color for readability */
  line-height: 1.6; /* Optional: improve readability */
}

/* Set Lora as the serif font for titles and headings */
h1, h2, h3, h4, h5, h6 {
  font-family: 'Lora', serif;
  font-weight: 700; /* Bold weight for headings */
  color: #222; /* Optional: make headings stand out */
}
  
  .ContactPage {
    max-width: 1000px;
    padding: 20px;
    align-items: center;       
    justify-content: center; 
    margin: 0 auto; /* Centers horizontally */
    text-align: center; /* Centers content inside */
  }

  .ContactContent {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
    align-items: center;       
    justify-content: center; 
    text-align: center;
    margin-top: 25px;
}


.ContactEmoji {
  font-size: 2.7em;
}

.ContactTitle h1 {
  font-size: 2.0em;
  margin-bottom: 10px;
}

.ContactTitle p {
  font-size: 1em;
  line-height: 1.6;
  color: #555;
  line-height: 1.6;
  margin-top: 10px;
  }


.ContactCards {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* Two equal-width columns */
    gap: 20px; /* Space between cards */
    border-radius: 16px;
    padding: 30px;
    padding-bottom: 20px;
    text-align: left;
  }
  
  .ContactCard {
    background-color: #40345c31;
    padding: 20px;
    border-radius: 12px;
  }

  .ContactCard a {
    color: #333; /* Change this to your desired color */
    text-decoration: none; /* Removes underline */
    font-weight: bold;
    padding-left: 5px;
  }
  
  .ContactCard a:hover {
    color: white; /* Hover color for the link */
  }
  

  @media (max-width: 768px) {
    .ContactCards {
      display: grid;
      grid-template-columns: repeat(1, 1fr); /* Two equal-width columns */
      gap: 20px; /* Space between cards */
      border-radius: 16px;
      padding: 30px;
      padding-bottom: 20px;
      text-align: left;
    }
  }



  



