/* General Typography */
body {
    font-family: 'Roboto', 'Arial', sans-serif;
    font-weight: 400;
    color: #333;
    line-height: 1.6;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Lora', serif;
    font-weight: 700;
    color: #222;
}

/* Page Layout */
.AmiciPage {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
}

.AmiciContent {
    text-align: center;
    margin-top: 30px;
}

/* Logo Styling */
.AmiciLogo {
    width: 150px;
    height: auto;
    margin-bottom: 30px;
}

/* Info Section */
.AmiciInfoSection {
    margin-top: 20px;
    text-align: left;
    padding-bottom: 25px;
}

.AmiciInfoContent {
    background-color: rgba(64, 52, 92, 0.19);
    padding: 20px;
    border-radius: 12px;
    transition: transform 0.2s ease, background-color 0.2s ease;
}

.AmiciInfoContent a {
    color: #333;
    text-decoration: none;
    font-weight: bold;
}

.AmiciInfoContent a:hover {
    color: #fff;
}

/* Phone Previews */
.AmiciPhonePreviews {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 16px;
}

.AmiciPhonePreviews img {
    width: 100%;
    object-fit: cover;
    border-radius: 8px;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.AmiciPhonePreviews img:hover {
    transform: scale(1.05);
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
}

/* Responsive Design */
@media (max-width: 768px) {
    .AmiciPhonePreviews {
        grid-template-columns: 1fr;
        gap: 20px;
        width: 50%;
        height: auto;
        margin: 0 auto;
        text-align: center;
    }

    .AmiciContent {
        padding: 10px;
    }
}
