/* Navbar Styling */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f4f2ff;
  position: sticky;
  top: 0;
  z-index: 1000;
  padding: 20px;
}

/* Logo Styling */
.logo {
  font-size: 1.2em;
  font-weight: semibold;
  color: #555;
  letter-spacing: 0.75px;
}

.large-screen {
  display: block; /* Visible on large screens */
}

.small-screen {
  display: none; /* Hidden on large screens */
}

/* Navigation Links Styling */
.nav-links {
  display: flex;
  gap: 50px;
}

.nav-links a {
  color: #555;
  text-decoration: none;
  font-size: 1em;
  font-weight: 500;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: white;
}

/* Responsive Navbar Adjustments */
@media (max-width: 768px) {

  .nav-links {
    display: block; 
    cursor: pointer;
    background: none;
    border: none;
    color: #555;
    margin: 0 auto; /* Centers horizontally */
    text-align: center; /* Centers content inside */
  }
  

  .logo {
    display: none;
  }

}
