/* Set Roboto as the default sans-serif font for body text */
body {
  font-family: 'Roboto', sans-serif;
  font-weight: 400; /* Regular weight for body */
  color: #333; /* Optional: adjust color for readability */
  line-height: 1.6; /* Optional: improve readability */
}

/* Set Lora as the serif font for titles and headings */
h1, h2, h3, h4, h5, h6 {
  font-family: 'Lora', serif;
  font-weight: 700; /* Bold weight for headings */
  color: #222; /* Optional: make headings stand out */
}
  
  .AboutPage {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
    align-items: center;       
    justify-content: center; 
  }

  .AboutContent {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
    align-items: center;       
    justify-content: center; 
    text-align: left;
    margin-top: 25px;
    margin: 0 auto; /* Centers horizontally */
    text-align: center; /* Centers content inside */
}


.AboutEmoji {
  font-size: 2.7em;
}

.AboutTitle {
  font-size: 2.0em;
  margin-bottom: 20px;
}

.AboutBio {
  margin: 20px 0;
  font-size: 1.2em;
  line-height: 1.6;
}

.AboutBio p {
    font-size: 0.8em;
    line-height: 1.6;
    color: #555;
    line-height: 1.6;
    margin-top: 10px;
  }


  .AboutBio a {
    color: #333; /* Change this to your desired color */
    text-decoration: none; /* Removes underline */
    font-weight: bold;
}

.AboutPhotos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 25px; 
    padding-bottom: 20px;
  }
  
  .AboutPhotos  img{
    width: 300px;
    height: 300px;
    border-radius: 20px;
    object-fit: cover;
    padding: 20px;
    background-color: #40345c31;
  }


  /* Responsive Navbar Adjustments */
@media (max-width: 768px) {

  .AboutPhotos {
    display: grid;
    grid-template-columns: 1fr; /* Single column */
    grid-template-rows: repeat(3, auto); /* Three rows */
    gap: 25px; 
    padding: 30px;
    border-radius: 20px;
    box-sizing: border-box; /* Ensures padding is included in width */
    width: 100%; /* Adjust width if needed */
    max-width: 100%; /* Prevents overflow on smaller screens */
  }
  
  .AboutPhotos img {
    width: 100%; /* Responsive to container width */
    height: auto; /* Maintains aspect ratio */
    max-height: 300px; /* Limit height if necessary */
    object-fit: cover; /* Scales image to fit without cropping */
  }
  
}


