/* Set Roboto as the default sans-serif font for body text */
body {
    font-family: 'Roboto', sans-serif;
    font-weight: 400; /* Regular weight for body */
    color: #333; /* Optional: adjust color for readability */
    line-height: 1.6; /* Optional: improve readability */
  }
  
/* Set Lora as the serif font for titles and headings */
h1, h2, h3, h4, h5, h6 {
    font-family: 'Lora', serif;
    font-weight: 700; /* Bold weight for headings */
    color: #222; /* Optional: make headings stand out */
  }

.SpeedworkPage {
    max-width: 1000px;
    margin: 0 auto;
    padding: 20px;
}

.SpeedworkContent {
    text-align: center;
    margin-top: 30px;
}


.speedworkLogo {
    height: 50px;
    width: 200px;
}

.SpeedworkInfoSection {
    margin-top: 20px;
    text-align: left;
    padding-bottom: 25px;
}

.SpeedworkInfoSection h1 {
    color: #333;
    text-align: left;
    margin-left: 50px;
    padding-bottom: 10px;
}

.SpeedworkInfoContent {
    background-color: #40345c31;
    padding: 20px;
    border-radius: 12px;
    transition: transform 0.2s ease;
}


.SpeedworkInfoContent a {
    color: #333; /* Change this to your desired color */
    text-decoration: none; /* Removes underline */
    font-weight: bold;
}

.SpeedworkInfoContent a:hover {
    color: white; /* Change this to your desired color */
    text-decoration: none; /* Removes underline */
    font-weight: bold;
}

.SpeedworkPhonePreviews {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 16px; 
  }
  
  .SpeedworkPhonePreviews img {
    width: 100%;
    object-fit: cover;
  }
  

  @media (max-width: 768px) {

    .SpeedworkPhonePreviews {
      display: grid;
      grid-template-columns: 1fr; /* Single column */
      grid-template-rows: repeat(4, auto); /* Three rows */
      gap: 10px; 
      width: 70%; /* Adjust width if needed */
      max-width: 70%; /* Prevents overflow on smaller screens */
      margin: 0 auto; /* Centers horizontally */
      text-align: center; /* Centers content inside */
    }
}